import Vue from 'vue'
import Router from 'vue-router'
//import Home from '@/components/Home'
import Home_plus from '@/components/Home_plus'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home_plus',
      component: Home_plus
    }
  ]
})



